import React, { Fragment } from 'react';
import PropTypes from 'prop-types';

import Input from './Input';
import Select from './Select';
import FileInput from './FileInput';
import PopInput from './popupInput';
import { OTPInputComponent } from './opt';

const Step = ({ data, onChange, onFileChange,onRadioBtnChange, onOtpChange,onStepChange, errors, stepKey, step, onPrevStep }) => {
  let output = [];

  for(const [key, val] of Object.entries(data)) {
    if(val.type.split(':')[0] === 'input') {
      output.push(<Input 
        key={key}
        placeholder={val.placeholder}
        name={key}
        value={val.value}
        onChange={(e) => onChange(stepKey, e)}
        error={errors[key]}
        type={val.type.split(':')[1]}
        label={val.label}
        showMic={val.showMic}
      />);
    }else if(val.type === 'select') {
      output.push(<Select 
        key={key}
        name={key}
        value={val.value}
        onChange={(e) => onChange(stepKey, e)}
        error={errors[key]}
        choices={val.choices}
        label={val.label}
      />);
    }else if(val.type === 'file') {
      output.push(<FileInput 
        key={key}
        onChange={onFileChange}
        error={errors[key]}
        name={key}
        stepKey={stepKey}
        fileName={val.fileName}
        label={val.label}
      />);
    }
    else if(val.type === 'date') {
      output.push(<FileInput 
        key={key}
        placeholder={val.placeholder}
        name={key}
        value={val.value}
        onChange={(e) => onChange(stepKey, e)}
        error={errors[key]}
        type={val.type.split(':')[1]}
        label={val.label}
        showMic={val.showMic}
      />);

    }
    else if(val.type === 'popup') {
      output.push(<PopInput 
        key={key}
        placeholder={val.placeholder}
        name={key}
        value={val.value}
        onChange={(e) => onChange(stepKey, e)}
        onRadioChange={onRadioBtnChange}
        error={errors[key]}
        stepKey={stepKey}
        type={val.type.split(':')[1]}
        choices={val.choices}
        label={val.label}
        showMic={val.showMic}

      />);
    }
    else if(val.type === 'opt') {
      output.push(<OTPInputComponent 
        key={key}
        placeholder={val.placeholder}
        name={key}
        error={errors[key]}
        type={val.type.split(':')[1]}
        autoFocus
        isNumberInput
        length={6}
        className="otpContainer"
        inputClassName="otpInput"
        onChangeOTP={(otp) => onOtpChange(otp,stepKey,key)}
        label={val.label}
        
      />);
    }
  }
  
  return(
    <Fragment>
      {output}
      {step > 1 && <a  className="backBtn mr-2" onClick={() => onPrevStep(step - 1)}>&#8592;</a>}
      <div className="has-text-centered">
      <button type="button" className="button btnxt nextBtn" onClick={(e) => onStepChange(data, e)}>Next</button>
      </div>

    </Fragment>
  );
}

Step.propTypes = {
  data: PropTypes.object.isRequired,
  onChange: PropTypes.func.isRequired,
  onFileChange: PropTypes.func,
  onStepChange: PropTypes.func.isRequired,
  errors: PropTypes.object,
  stepKey: PropTypes.string.isRequired,
  step: PropTypes.number.isRequired,
  onPrevStep: PropTypes.func
}

export default Step;