import React, { Fragment } from 'react'

function RadioWithText({radioBtn=[{label: 'Google',value: 'Google'},{label: 'Facebook',value: 'Facebook'}],handleChange,stepKey,name,textOnly=true}) {
    const handleClickBtn =(val,label)=>{
        handleChange(stepKey,{target:{name:label,value:val}},name)
    }
    const handleChangeBtn = (e)=>{
        handleChange(stepKey,e,name);
    }
    return (
        <Fragment>
            
  
        {radioBtn.map(({value,label,checked})=>{
            return(
                <div className={`column is-${textOnly?'12':'6'}-mobile bg-color`} key={label}>
             <div className={`${textOnly?'control_text':'control'}`} onClick={()=>handleClickBtn(value,label)}>
            {
            textOnly?
            <h1 className={`label_class ${checked&&'selected_label'}`}> {label}</h1>
            : <label className="radio ">
            <input type="radio" name={label} className="mr-2"   value={value}  
                                   checked={checked} 
                                   onChange={handleChangeBtn}  />
    {label}
  </label>
            }
       
                </div>
                </div>
            )
        })
        }
        </Fragment>
    )
}

export default RadioWithText
