import React, { useState } from 'react';
import PropTypes from 'prop-types';
import poupIcon from '../imgs/popup.png';
import './popupInput.css'
import PopupContent from './popupContent';
import micIcon from '../imgs/mic.png';


const PopInput = ({ type = 'text', placeholder,stepKey, name, value, onChange, error,choices,onRadioChange ,label,showMic=false}) => {
    
    const [open, setOpen] = useState(false);

    const openPopup = ()=>{
        setOpen(true)};
    const closePopUp = ()=>{
        setOpen(false);
    }
  return(
    <div className="field  popup_section">
    <label className="label flow_label ">{label}</label>
    <div className="control  has-icons-right">
      <input
      className={error ? "input flow_input   is-danger" : "input flow_input "}
      style={{cursor: "pointer"}}
      
         type={type} 
        placeholder={placeholder}
        name={name}
        value={value}
        onChange={onChange}
        onFocus={openPopup}
        autoComplete="off" /> 
      <span className="icon is-small is-right">
        <img src={poupIcon} alt="popupIcon"  />
      </span>
      <span style={{position: "absolute",right:'-34px'}}>
   {showMic&&<img src={micIcon} alt={"mic"} style={{width:'35px',paddingTop:'3px'}} />} 
  </span>  
    </div>
    
    {error && <div className="has-text-danger-dark">{error}</div>}
    <div className={`modal ${open&&'is-active'}`}>
  <div className="modal-background"></div>
  <div className="modal-content popup_content">
      <div className="modal-header">
      <span>Choose One Option</span>
   <a  className="delete is-large " onClick={closePopUp}>X</a>
      </div>
    <PopupContent handleChange={onRadioChange} closePopUp={closePopUp} showMic={showMic} name={name} stepKey={stepKey} radioBtn={choices} />
  </div>
</div>
  </div>
    // <div className="mb-5">
    //   <input
    //     className={error ? "input is-danger" : "input"} 
    //     type={type} 
    //     placeholder={placeholder}
    //     name={name}
    //     value={value}
    //     onChange={onChange}
    //     autoComplete="off"
    //   />
    //   {error && <div className="has-text-danger-dark">{error}</div>}
    // </div>
  );
}

PopInput.propTypes = {
  type: PropTypes.string,
  placeholder: PropTypes.string,
  name: PropTypes.string.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.object
  ]),
  onChange: PropTypes.func.isRequired
}

export default PopInput;