import React, { Fragment } from 'react';

const Preview = ({ data, onPrevStep }) => {
  return(
    <div className="panel is-primary">
      <p className="panel-heading">Your data</p>
      <div className="panel-block is-block">
        <ul className="py-5">
          {data.map((input, index) => (
            <li key={index} className="py-2 li_title">
              {!input.image
                ? <Fragment><strong>{input.label}:</strong> {input.value}</Fragment>
                : <div><strong>{input.label}:</strong> <img src={input.value} alt="value Img" style={{maxWidth: '100px'}} /></div>
              }
            </li>
          ))}
        </ul>
        <div>
          <a  className=" backBtn mr-2" onClick={onPrevStep}>&#8592;</a>
          <button type="submit" className="button is-primary">Submit form</button>
        </div>
      </div>
    </div>
  );
}

export default Preview;