import React from 'react';
import PropTypes from 'prop-types';
import micIcon from '../imgs/mic.png';

const Input = ({ type = 'text', placeholder, name, value, onChange, error ,showLabel=true,label,showMic }) => {
  
  return(
    <div className="field mb-2">
  <label className="label flow_label">{label}</label>
  <div className={`control`} style={{display:'inline'}}>
    {type ==='date'? <input
        className={error ? "input flow_input is-danger" : "input flow_input"} 
        type={type} 
        placeholder={placeholder}
        name={name}
        value={value}
        max={getTodayDate()}
        onChange={onChange}
        autoComplete="off"
      />: <input
      className={error ? "input flow_input is-danger" : "input flow_input"} 
      type={type} 
      placeholder={placeholder}
      name={name}
      value={value}
      onChange={onChange}
      autoComplete="off"
    />}

      <span>
   {showMic&&<img src={micIcon} alt={"mic"} style={{width:'35px',paddingTop:'3px'}} />} 
  </span>   
  </div>
  {error && <div className="has-text-danger-dark">{error}</div>}

</div>
    // <div className="mb-5">

     
    // </div>
  );
}

Input.propTypes = {
  type: PropTypes.string,
  placeholder: PropTypes.string,
  name: PropTypes.string.isRequired,
  value: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.object,
    PropTypes.number
  ]),
  onChange: PropTypes.func.isRequired
}
function getTodayDate(){
  let today = new Date(),
    day = today.getDate(),
    month = today.getMonth()+1, //January is 0
    year = today.getFullYear();
         if(day<10){
                day='0'+day
            } 
        if(month<10){
            month='0'+month
        }
         today = year+'-'+month+'-'+day;
         return today;
}

export default Input;